.sectionclass {/* here section */
    display: flex;
    flex-wrap: wrap;
    justify-content:center;
    align-items: center;
    text-align: center;

  }

  .single_thumbnail {/*div of section*/
    float: left;
    height: auto;
    justify-content: center;
    text-align: center;


  }
  .imagelink {
    width: 60%;
    text-decoration: none;
    height: auto;

  }
  .imagediv {
    max-width:100%;
    height: auto;
    padding: 20px;
    text-align: center;
    align-items: center;
    justify-content: center;
    /* Adjust as needed */
  }

  .image {
    width: 400px;
    height: auto;
    border-radius: 20px;
    justify-content: center;
    margin: auto;
    display:block;
    margin: auto;
    transition: transform 0.3s ease;
  }
  .image:hover{
    transform: translateY(-10px);
  }

  .details {
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .title{
    font-style: 'roboto';
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: rgb(26, 26, 25);
    text-decoration: none;
  }
  @media screen and (max-width:800px) {
    .cover-image{
      width: 100%;
    }
    
  }