/* Ensures the parent container scales responsively */
.nail {
    width: 100%;
    max-width: 100%; /* Ensures it doesn't exceed the viewport width */
    height: auto;   /* Maintains aspect ratio */
    position: relative;
    padding-top: 56.25%; /* 16:9 aspect ratio (56.25% = 9/16 * 100) */
}

/* Positions the video absolutely within its parent to fill it completely */
.video_ {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: fill;
    border-radius: 10px; /* Optional: scales and crops the video to fill container */
}

/* Optional: Basic styling for other elements */
.heading {
    font-size: 2rem;
    text-align: center;
}

.space_div {
    height: 20px; /* Adjust as needed */
}

.section_video {
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Media query for desktop screens */
@media (min-width: 1024px) {
    .nail {
        width: 100%;
        max-width: 1280px; /* Adjust as needed for max width on desktop */
        height: 800px; /* Fixed height for desktop screens */
        padding-top: 0; /* Remove padding-top for fixed height */
        margin: 0 auto; /* Center the container */
    }

    .video_ {
        width: 100%;
        height:500px;
        object-fit: contain; /* Ensure video fits within the container without cropping */
    }
}
