
.App {
  display: flex;
  flex-direction: column;
  background-color: #ffffff; 
  text-align: center;
  justify-content: center;
  align-items: center;
  
/* White background */
}

.Menu {
  width: 100%;
  align-items: center;
  background-color: #ffffff; /* Dark color for menu background */
  color: #000000; /* Text color for menu items */
  padding: 10px 0; /* Add padding to the top and bottom */
}

.Menu ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.Menu ul li {
  display: inline;
  margin-right: 20px;
}

.Content {
  margin-top: 20px;
  align-items: center;
  justify-content: center;
  display: block; /* Add some space between menu and content */
}


.white{
  color: white;
}

.InstagramLogo {
  height: 20px; /* Adjust the height as needed */
  width: auto; /* Maintain aspect ratio */
  display: inline-block;
  margin-right: 10px; /* Adjust spacing between logo and text */
  vertical-align: middle;
  text-decoration: none; /* Align with text vertically */
}

.Menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
}


.Menu ul li.instagram {
  margin-right: 20px; /* Remove margin for Instagram logo */
}
.Name{
  align-items: center;
